*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  height: 100vh;
}
.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.btn{
  font-size: 1rem;
  padding: 10px;
  margin-top: 20px;
  width: 100px;
  border-radius: 5px;
  outline: none;
  border: none;
  cursor: pointer;
}